module.exports = store

function store (state, emitter) {
  state.outputTape = []
  state.code = "cool a autoschadehersteller International beanie!Ik"
  state.tapeLength = "10"
  
  emitter.on('DOMContentLoaded', function () {
    emitter.on('outputTape:replace', function (output) {
      state.outputTape = output
      emitter.emit(state.events.RENDER)
    })
  })
}
