var html = require('choo/html')

var Logo = require("../components/wwLogo.js")
var logo = new Logo()

var TITLE = 'wordwang demo | darcylf'
module.exports = view

function view (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)

  return html`
    <body class="awsm">
      <main>
        <h1 aria-label="wordwang">${logo.render()}</h1>
        <p>a language-agnostic word-based eso-lang.</p>
        <p><a href="https://github.com/ehne/wordwang">more information about wordwang.</a></p>
      </main>
      <main>
        
        <section id="demo" >
          <h2 style="display:none">Demo:</h2>
          <p></p>
          <fieldset>
          <legend>[ wordwang demo ]</legend>
          <form onsubmit=${_onSubmit}>
              <label for="ww-code">Your wordwang code:</label>
              <textarea name="ww-code" rows="4">${state.code}</textarea>
              <label for="ww-tape-length">The tape length: </label>
              <input type="number" id="ww-tape-length" value="${state.tapeLength}" name="ww-tape-length">
              <button type="submit">Send</button>
          </form>
          
          <form>
          <p></p>
          <label for="ww-output">Output tape:</label>
          <input type="text" id="ww-output" value="${state.outputTape}" readonly="readonly">
  
          </form>
          </fieldset>
        </section>
      </main>
      <footer>
        <p>made by <a href="https://darcylf.me">darcylf</a> in 2020</p>
      </footer>
    </body>
  `
  function  _onSubmit (e) {
    e.preventDefault()
    var form = e.currentTarget
    var data = new FormData(form)                                       // 2.
    var headers = new Headers({ 'Content-Type': 'application/json' })   // 3.
    var body = {}
    for (var pair of data.entries()) body[pair[0]] = pair[1]            // 4.
    console.log(body)
    // keeps entered values
    state.code = body["ww-code"]
    state.tapeLength = body["ww-tape-length"]
    // now we run the wordwangarator
    var wangedwords = wordwang(body)
    
    emit('outputTape:replace', wangedwords)
    
  }

  function wordwang(inputData){
    var headPos = 0
    var instructionPos = 0
    var tapeLength = parseInt(inputData["ww-tape-length"])
    var instructions = inputData["ww-code"]
    var tape = new Array(tapeLength).fill(0);
    var inst = convToInst(instructions)
    console.log(inst)
    console.log(tape)
    prog: {
    while (instructionPos <= inst.length-1) {
      ///console.log("")
      var currentInstruction = inst[instructionPos];
      //console.log(`tape after instructionPos ${instructionPos}:`)
      switch (currentInstruction[0]) {
        case 0:
          try {
            tape[headPos] = (tape[headPos] + 1) % 2
          } catch (error) {
            break prog;
          }
         
          instructionPos += 1
          //console.log("instruction: 0")

          break;
        case 1:
          headPos += 1
          instructionPos += 1
          //console.log("instruction: 1")
          break;
        case 2:
          headPos -= 1
          instructionPos += 1
          //console.log("instruction: 2")
          break;
        case 3:
          if (tape[headPos]==1){
            instructionPos = currentInstruction[1]-1
          } else {
            instructionPos +=1
          }
          break;
        default:
          instructionPos += 1
          break;
      }
      //console.log(`tape is at: ${headPos}`)
      //console.log(`${tape}`)
    }}
    //tape.pop()
    return(tape.slice(0,state.tapeLength))
  }
  // splits the string by spaces, then splits the strings into another array by "!"
  function splitWordsOut(_i){
    var output = [];
    _i.split(" ").forEach(el => {
      output.push(el.split("!")) 
    });
    return(output)
  }

  function convToInst(_i){
    var outInstructions = []
    var splitWordsList = splitWordsOut(_i)
    // goes through and calculates what function to use
    splitWordsList.forEach(function (el, index){
      var instructionType = Math.round(Math.sqrt(el[0].length +index))%4
      //console.log(instructionType)
      var instructionList = [instructionType]
      if (el.length >1){
        instructionList.push(el[1].length)
      }
      outInstructions.push(instructionList)
    })
    console.log(outInstructions)
    return(outInstructions)
  }

}


